interface IProps {
	value: string;
	numChars: number;
}

const listOfAbbreviations = [
	{ value: "LowPass" },
	{ value: "HighPass" },
	{ value: "Sidechain", abbr: "SC" },
	{ value: "Output", abbr: "Out" },
];

function camelize(str: string) {
	return str.replace(/(\w)(\w*)/g, function (g0, g1, g2) {
		return g1.toUpperCase() + g2.toLowerCase();
	});
}

export const TruncatedText: React.FC<IProps> = ({ numChars, value }) => {
	function RemoveVowels(str: string) {
		return str.replace(/[aeiou]/g, "");
	}

	function ContainsPunctuation(str: string) {
		return str.match(/[^\w\s]/gi) != null;
	}

	function RemovePunctuation(str: string) {
		return str.replace(/[^\w\s]/gi, "");
	}

	const wordsToRemove = ["and"];
	const wordsToIgnore = ["Auto", "EQ"];

	function TruncateToCharacters(str: string, numChars: number) {
		var newString = RemovePunctuation(camelize(str.trim()));

		listOfAbbreviations.forEach((abbreviation) => {
			var regEx = new RegExp(abbreviation.value, "ig");
			if (abbreviation.abbr) {
				newString = newString.replace(regEx, abbreviation.abbr);
			} else {
				newString = newString.replace(
					regEx,
					abbreviation.value.replace(/[^A-Z]/g, "")
				);
			}
		});

		var words = newString.split(/\s/);

		var history = "";
		const isGood = () => {
			var total = 0;
			words.forEach((word) => {
				total += word.length;
			});
			return total <= numChars;
		};

		const getWord = () => ({
			truncatedText: words.join("").slice(0, numChars),
			history,
		});

		var wordsByLength = [...words].sort((a, b) =>
			a.length > b.length ? -1 : 1
		);

		history += `Remove vowels: ${wordsByLength.join(", ")}\r\n\r\n`;

		for (var i = 0; i < wordsByLength.length; i++) {
			if (isGood()) {
				break;
			}
			var word = wordsByLength[i];
			if (wordsToIgnore.includes(word)) break;
			var newWord = RemoveVowels(word);
			var idx = words.findIndex((value) => value === word);
			words[idx] = newWord;
			history += `${word} > ${newWord} = ${words.join("")}\r\n`;
		}

		wordsByLength = [...words].sort((a, b) =>
			a.length > b.length ? -1 : 1
		);

		history += `\r\nSlice: ${wordsByLength.join(", ")}\r\n\r\n`;

		for (var i = 0; i < wordsByLength.length; i++) {
			if (isGood()) {
				break;
			}
			var word = wordsByLength[i];
			if (wordsToIgnore.includes(word)) break;
			var shortestWord = wordsByLength[wordsByLength.length - 1];
			var newWord = word.slice(0, shortestWord.length - 1);
			var idx = words.findIndex((value) => value === word);
			words[idx] = newWord;
			history += `${word} > ${newWord} = ${words.join("")}\r\n`;
		}

		return getWord();
	}

	const { truncatedText, history } = TruncateToCharacters(value, numChars);

	return <span title={history}>{truncatedText}</span>;
};
